import axios from "axios";
import { useState, useCallback } from "react";

export const useGet = (url, initalState = null) => {
    const [data, setData] = useState(initalState);
    const [loaded, setLoaded] = useState(false);
    const [loading, setLoading] = useState(false);
    const [success, setSuccess] = useState(false);
    const [error, setError] = useState(null);


    const perform_get = useCallback(async (params = {}) => {
        setLoading(true);
        try {
            let res = await axios.get(url, {
                params
            });
            setData(res.data);
            setSuccess(true);
            setError(null);
        } catch (error) {
            setSuccess(false);
            setError(error?.response?.data ? error?.response?.data : "Error Occured");
        } finally {
            setLoading(false);
            setLoaded(true);
        }
    }, [url])

    const reset = useCallback(() => {
        setData(initalState);
        setLoaded(false);
        setLoading(false);
        setSuccess(false);
        setError(false);
    }, [initalState])

    return { data, loaded, setLoaded, loading, success, error, perform_get, reset };
}
