import { useState } from 'react'
import SearchBox from './SearchBox'
import StudentProfile from './StudentProfile'

const Home = (props) => {
    const [dept, setDept] = useState(localStorage?.getItem('dept') || 'cse');
    if (props.searchBarOpen) {
        return (
            <SearchBox
                setStudentData={props.setStudentData}
                closeSearchbar={props.closeSearchbar}
                dept={dept}
                setDept={setDept}
                handleSnackbarOpen={(msg, severity) => props.handleSnackbarOpen(msg, severity)}
            />
        )
    } else {
        return (
            <StudentProfile
                studentData={props.studentData}
                dept={dept}
            />
        )
    }

}

export default Home