import { Card, Typography, Stack, Chip } from "@mui/material"
import PersonIcon from '@mui/icons-material/Person';
import HistoryIcon from '@mui/icons-material/History';
import dateFormat from "dateformat";

const DocHistory = ({doc}) => {
  return (
    <Card
        sx={{
            p: 1,
            mb: 1,
            borderTop: '1px solid #0077b6',
            // borderBottom: '1px solid #0077b6',
            borderLeft: '4px solid #0077b6'
        }}
        elevation={1}
    >
        <Typography variant="body1">{doc.reference}</Typography>
        <Typography variant="body2" color='text.secondary'>{`${doc.registration} - ${doc.doc_type}`}</Typography>
        <Stack
            direction='row'
            spacing={1}
        >
            <Chip 
                icon={<PersonIcon />}
                label={doc.accessed_by}
                size="small"
            />
            <Chip 
                icon={<HistoryIcon />}
                label={dateFormat(doc.added, 'mmmm dS, yyyy, h:MM:ss TT')}
                size="small"
                variant="outlined"
                
                color="primary"
            />
        </Stack>
    </Card>
  )
}

export default DocHistory