import { React, useState } from 'react';
import { Button, Stack, Paper, Chip, Typography } from "@mui/material";
import DownloadRoundedIcon from '@mui/icons-material/DownloadRounded';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import axios from 'axios';
import { Modal, Empty } from 'antd';
import * as urls from '../../BackendUrls';

export function getCookie(name) {
    let cookieValue = null;
    if (document.cookie && document.cookie !== '') {
        const cookies = document.cookie.split(';');
        for (let i = 0; i < cookies.length; i++) {
            const cookie = cookies[i].trim();
            // Does this cookie string begin with the name we want?
            if (cookie.substring(0, name.length + 1) === (name + '=')) {
                cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                break;
            }
        }
    }
    return cookieValue;
}

const CustomTool = (props) => {
    const [selectedFile, setSelectedFile] = useState(null);
    const [modalOpen, setModalOpen] = useState(false);
    const [savedData, setSavedData] = useState([]);
    
    const handleFileChange = (event) => {
        setSelectedFile(event.target.files[0]);
    };

    const handleUpload = async () => {
        if (selectedFile) {
            try {
                const formData = new FormData();
                formData.append('file', selectedFile);
                await axios.post(urls.exportExcelUrl, formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                        'X-CSRFToken': getCookie('csrftoken')
                    },
                });
                props.handleSnackbarOpen('Complete', 'success');
            } catch (error) {
                props.handleSnackbarOpen('Error uploading file', 'error');
            }
        } else {
            props.handleSnackbarOpen('No file selected', 'warning')
        }
    };

    const getSavedData = async () => {
        try {
            const response = await axios.get(urls.viewSavedDataUrl);
            const data = await response.data;
            setSavedData(data)
        } catch (error) {
            props.handleSnackbarOpen('Error fetching data', 'error')
        }
    }

    const handleModalOpen = (e) => {
        getSavedData();
        setModalOpen(true);
    };
    const handleModalClose = (e) => {
        setModalOpen(false);
        setSavedData([]);
    };
    return (
        <div className='mt-5' style={{ textAlign: 'left' }}>
            <Modal title="Saved Manual Data" open={modalOpen} onCancel={handleModalClose} footer={null}>
                {
                    savedData.length > 0 ?
                        savedData.map(session => (
                            <Paper sx={{ p: 2, mt: 1 }} elevation={4}>
                                <Typography textAlign="center" variant='h6' sx={{ mb: 1 }}>{session.session}</Typography>
                                {
                                    session.students.map(reg => (
                                        <Chip color='primary' sx={{ m: 0.2, px: 0.3 }} label={reg} />
                                    ))
                                }
                            </Paper>
                        ))
                        : <Empty />
                }
            </Modal>
            <Stack direction="row" justifyContent='flex-end'>
                <Button onClick={handleModalOpen}>Saved Manual Data</Button>
            </Stack>
            <div className="py-3 mb-5" >
                <div className="text-muted fs-5 text-center mb-2">Instructions</div>
                <ul className="mb-0">
                    <li className="text-success ">Use the provided excel workbook as a template.</li>
                    <li className="text-success ">Do not alter the field names, enter only the corresponding data.</li>
                    <li className="text-success ">Maintaining the order of the sheets is not necessary but don't change the name of the sheets.</li>
                    <li className="text-success ">Verify the data and the specified formatting before exporting.</li>
                </ul>
                <div className="container row-flexer mt-3">
                    <Button variant="contained" color="primary" href={urls.excelTemplateUrl} target="_blank" startIcon={<DownloadRoundedIcon />}>
                        Download Template
                    </Button>
                </div>
            </div>
            <div className="mb-3">
                <label for="formFile" className="form-label">Select Excel File</label>
                <input onChange={e => handleFileChange(e)} className="form-control" accept=".xlsx" type="file" id="excelFile" />
                <div className="d-flex justify-content-end mt-2">
                    <Button onClick={handleUpload} color='secondary' style={{ borderRadius: "180px", padding: "5px 25px" }} variant="contained" startIcon={<CloudUploadIcon />}>
                        Export
                    </Button>
                </div>
            </div>
        </div>
    )
}

export default CustomTool