import { useState, useEffect } from 'react'
import {
    Card, TextField, CardMedia, CardContent, Stack,
    Button, Typography, CardActions, Box, Grid
} from '@mui/material'
import DownloadIcon from '@mui/icons-material/Download';
import * as urls from '../../BackendUrls';

const resPubDateKeys = {
    eee: 'published__eee',
    cse: 'published__cse',
    ce: 'published__ce',
}

const StudentProfile = ({ studentData, dept }) => {
    const [resultPubDate, setResPubDate] = useState(localStorage.getItem(resPubDateKeys[dept]) || '');
    const [preparedBy, setPreparedBy] = useState(localStorage.getItem('preparedBy') || '');
    const [principalInfo, setPrincipalInfo] = useState(localStorage.getItem('principal') || '');
    const [references, setReferences] = useState(
        {
            testimonial: localStorage.getItem('testimonial') || '',
            coursemedium: localStorage.getItem('coursemedium') || '',
            appeared: localStorage.getItem('appeared') || ''
        }
    );
    // const handleInputChange = event => {
    //     let value = event.target.value;
    //     if (value.length > 0) {
    //         setFileUrls({
    //             testimonial: urls.baseUrl + studentData.testimonial_url + "?ref=" + value,
    //             coursemedium: urls.baseUrl + studentData.coursemedium_url + "?ref=" + value,
    //             appearedCert: urls.baseUrl + studentData.appearedCert_url + "?ref=" + value,
    //         })
    //     } else {
    //         setFileUrls({ testimonial: null, coursemedium: null, appearedCert: null })
    //     }
    // }

    useEffect(() => {
        localStorage.setItem(resPubDateKeys[dept], resultPubDate);
    }, [resultPubDate, dept])

    useEffect(() => {
        localStorage.setItem('principal', principalInfo);
    }, [principalInfo])

    useEffect(() => {
        Object.keys(references).forEach(key => {
            localStorage.setItem(key, references[key]);
        })
    }, [references])

    const handleResPubDateChange = e => {
        setResPubDate(e.target.value);
    }

    const handleRefChange = e => {
        setReferences(prevState => (
            {
                ...prevState,
                [e.target.name]: e.target.value
            }
        ))
    }

    const urlSuffix = `&pub_date=${resultPubDate}&preparedby=${preparedBy}&principal=${principalInfo}`

    return (
        <Box className="mt-5 mb-5">
            <Grid container spacing={4}>
                <Grid item xs={12} md={7} >
                    <div className="mb-5 bg-light shadow-sm py-3 rounded">
                        <div className="text-center" style={{ paddingTop: '2rem' }}>
                            <img src={urls.baseUrl + studentData.profile_picture_url} alt="avatar"
                                className="rounded-circle img-fluid" style={{ width: '150px' }} />
                            <h5 className="my-3">{studentData.name}</h5>
                            <p className="text-muted mb-1">{studentData.registration}</p>
                            <p className="text-muted mb-1">Session: {studentData.session}</p>
                            <Typography gutterBottom variant="body1" component="div">
                                Department of {studentData.dept}
                            </Typography>
                        </div>
                    </div>
                    <TextField
                        value={resultPubDate}
                        onChange={handleResPubDateChange}
                        fullWidth
                        className='mb-3'
                        id="outlined-basic"
                        label="Result Publication Date"
                        variant="filled"
                    />
                    <TextField
                        value={principalInfo}
                        onChange={(e) => setPrincipalInfo(e.target.value)}
                        fullWidth
                        className='mb-3'
                        id="outlined-basic"
                        label="Principal Info (Name, Designation)"
                        variant="filled"
                    />
                    <TextField
                        value={preparedBy}
                        onChange={(e) => setPreparedBy(e.target.value)}
                        fullWidth
                        className='mb-3'
                        id="outlined-basic"
                        label="Prepared & Checked By"
                        variant="filled"
                    />
                    <Card className="py-3" sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, justifyContent: "center", alignItems: 'center', mb: 2 }}>
                        <CardMedia
                            sx={{ width: 90 }}
                            component="img"
                            image="/static/certificate.png"
                            title="thumbnail"
                        />
                        <CardContent className='py-2' sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, justifyContent: "center", alignItems: 'center' }}>
                            <Stack
                                justifyContent='flex-start'
                            >
                                <Typography variant="h5" component="div" sx={{ textAlign: { xs: 'center' } }}>
                                    Testimonial
                                </Typography>
                                <input
                                    name='testimonial'
                                    value={references.testimonial}
                                    onChange={handleRefChange}
                                    style={{
                                        padding: '5px',
                                        borderRadius: '5px',
                                        border: '1px solid gray'
                                    }}
                                    placeholder='Ref'
                                />
                            </Stack>
                            <CardActions>
                                <Button
                                    className="ms-3"
                                    target="_blank"
                                    href={`${urls.baseUrl}${studentData.testimonial_url}?ref=${references.testimonial}${urlSuffix}`}
                                    disabled={references.testimonial.length === 0 || resultPubDate.length === 0}
                                    variant='contained'
                                    startIcon={<DownloadIcon />}
                                >
                                    Download
                                </Button>
                            </CardActions>
                        </CardContent>
                    </Card>
                    <Card className="py-3" sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, justifyContent: "center", alignItems: 'center', mb: 2 }}>
                        <CardMedia
                            sx={{ width: 90 }}
                            component="img"
                            image="/static/certificate.png"
                            title="thumbnail"
                        />
                        <CardContent className='py-2' sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, justifyContent: "center", alignItems: 'center' }}>
                            <Stack
                                justifyContent='flex-start'
                            >
                                <Typography variant="h5" component="div" sx={{ textAlign: { xs: 'center' } }}>
                                    Course Medium Certificate
                                </Typography>
                                <input
                                    name='coursemedium'
                                    value={references.coursemedium}
                                    onChange={handleRefChange}
                                    style={{
                                        padding: '5px',
                                        borderRadius: '5px',
                                        border: '1px solid gray'
                                    }}
                                    placeholder='Ref'
                                />
                            </Stack>
                            <CardActions>
                                <Button
                                    className="ms-3"
                                    target="_blank"
                                    href={`${urls.baseUrl}${studentData.coursemedium_url}?ref=${references.coursemedium}${urlSuffix}`}
                                    disabled={references.coursemedium.length === 0}
                                    variant='contained'
                                    startIcon={<DownloadIcon />}
                                >
                                    Download
                                </Button>
                            </CardActions>
                        </CardContent>
                    </Card>
                    <Card className="py-3" sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, justifyContent: "center", alignItems: 'center', mb: 2 }}>
                        <CardMedia
                            sx={{ width: 90 }}
                            component="img"
                            image="/static/certificate.png"
                            title="thumbnail"
                        />
                        <CardContent className='py-2' sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, justifyContent: "center", alignItems: 'center' }}>
                            <Stack
                                justifyContent='flex-start'
                            >
                                <Typography variant="h5" component="div" sx={{ textAlign: { xs: 'center' } }}>
                                    Appeared Certificate
                                </Typography>
                                <input
                                    name='appeared'
                                    value={references.appeared}
                                    onChange={handleRefChange}
                                    style={{
                                        padding: '5px',
                                        borderRadius: '5px',
                                        border: '1px solid gray'
                                    }}
                                    placeholder='Ref'
                                />
                            </Stack>
                            <CardActions>
                                <Button
                                    className="ms-3"
                                    target="_blank"
                                    href={`${urls.baseUrl}${studentData.appearedCert_url}?ref=${references.appeared}${urlSuffix}`}
                                    disabled={references.appeared.length === 0 || resultPubDate.length === 0}
                                    variant='contained'
                                    startIcon={<DownloadIcon />}
                                >
                                    Download
                                </Button>
                            </CardActions>
                        </CardContent>
                    </Card>

                </Grid>
                <Grid item xs={12} md={5}>
                    {
                        studentData.semesters.map(semester => (
                            <Card key={semester.semester_no} sx={{ mb: 2 }}>
                                <CardMedia
                                    sx={{ mt: 2 }}
                                    component="img"
                                    image={urls.baseUrl + semester.tabulation_thumb_img}
                                    title="thumbnail"
                                />
                                <CardContent>
                                    <Typography gutterBottom variant="h5" component="div">
                                        {semester.semester_name}
                                    </Typography>
                                </CardContent>
                                <CardActions sx={{ justifyContent: 'center', mb: 2 }}>
                                    <Button target="_blank" href={urls.baseUrl + semester.tabulation_url} variant='outlined' startIcon={<DownloadIcon />}>Download Tabulation</Button>
                                </CardActions>
                            </Card>
                        ))
                    }
                </Grid>
            </Grid>
        </Box>
    )
}

export default StudentProfile