import { useEffect, useState, useCallback } from "react";
import { Modal, Empty, Typography } from "antd";
import { docsHistoryUrl } from "../../BackendUrls";
import { Virtuoso } from "react-virtuoso";
import { Box, Stack, TextField, CircularProgress } from '@mui/material';
import HistoryIcon from '@mui/icons-material/History';
import { Chip } from '@mui/material'
import { useGet } from "../../hooks/useApi";
import DocHistory from "./DocHistory";

const HistoryModal = () => {
  const [open, setOpen] = useState(false);
  const { data, perform_get, loading, loaded, reset } = useGet(docsHistoryUrl, []);
  const [searchQuery, setSearchQuery] = useState('');
  const [filteredData, setFilteredData] = useState([]);

  useEffect(() => {
    if (open && !loaded) {
      perform_get();
    }
  }, [loaded, open, perform_get])

  useEffect(() => {
    if (searchQuery.length === 0) {
      setFilteredData(data);
      return;
    }
    const timeout = setTimeout(() => {
      setFilteredData(
        data.filter(itm => itm.registration.includes(searchQuery) || itm.reference.toLowerCase().includes(searchQuery.toLowerCase()))
      )
    }, 500)
    return () => {
      clearTimeout(timeout);
    }

  }, [searchQuery, data])

  const handleClose = useCallback(() => {
    setOpen(false);
    reset();
  }, [reset])

  return (
    <>
      <Chip
        sx={{
          mx: 2
        }}
        label="History"
        icon={<HistoryIcon />}
        onClick={() => setOpen(true)}
      />
      <Modal
        open={open}
        footer={null}
        onCancel={handleClose}
        title="Document history"
      >
        <Box>
          {
            !loaded || loading ?
              <Stack
                sx={{ my: 10 }}
                alignItems='center'
              >
                <CircularProgress />
              </Stack> :
              <>
                <TextField
                  fullWidth
                  label="Search"
                  color="success"
                  value={searchQuery}
                  sx={{ mt: 1 }}
                  onChange={e => setSearchQuery(e.target.value)}
                />
                <Stack
                  sx={{ my: 1 }}
                  alignItems='flex-end'
                >
                  <Typography
                    sx={{
                      textAlign: 'center'
                    }}
                  >
                    {filteredData.length} Document{filteredData.length === 1 ? '' : 's'}
                  </Typography>
                </Stack>
                {
                  filteredData.length > 0 ?
                    <Box
                      sx={{ height: '400px' }}
                    >
                      <Virtuoso
                        data={filteredData}
                        height='100%'
                        itemContent={(idx, doc) => (
                          <DocHistory doc={doc} />
                        )}
                      />
                    </Box> :
                    <Empty
                      image={Empty.PRESENTED_IMAGE_SIMPLE}
                    />
                }
              </>
          }
        </Box>
      </Modal>
    </>
  )
}

export default HistoryModal